.products {
  margin-top: 100px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.products__slider {
  margin-top: 75px;
  padding-bottom: 100px;
  overflow: hidden;
  width: 100%;
  text-align: center;
}

.products__item {
  height: 500px;
}

.products__info {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 85%;
  border: 1px #ced6af solid;
}

.products__cart {
  width: 100%;
  height: 15%;
  background-color: #f1f1f1;
  border-bottom: #e1e1e1;
  border-left: #e1e1e1;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.products__stars {
  display: flex;
  text-align: center;
}

.products__star {
  margin: 0 2px;
}

.products__name {
  margin-top: 15px;
  margin-bottom: 35px;
}

.products__price {
  padding-left: 30px;
}

.products__buy {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 31%;
  height: 100%;
  background-color: #6e774a;
}

.products__img-b {
  width: 200px;
  height: 230px;
}

.products__img {
  margin: auto;
  max-width: 100%;
  object-fit: cover;
}

.products .slick-slide {
  padding: 0 20px;
}

.products__next-arrow {
  position: absolute;
  bottom: -60px;
  right: 46%;
}

.products__prev-arrow {
  left: 46%;
  position: absolute;
  bottom: -60px;
}

.products__prev-arrow:hover, .products__next-arrow:hover {
  cursor: pointer;
}

.products__prev-arrow:active, .products__next-arrow:active {
  transform: scale(0.7, 0.7);
}

@media screen and (max-width: 1300px) {
  .products__next-arrow {
    right: 42%;
  }
  .products__prev-arrow {
    left: 42%;
  }
}
